* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Helvetica Neue, Arial, sans-serif;
}

header {
  background-color: #000;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 3rem;
  display: flex;
}

.color-frontend, option[value="frontend"] {
  color: #fff;
  background-color: #647764;
}

.color-backend, option[value="backend"] {
  color: #fff;
  background-color: #7c7885;
}

.color-ux, option[value="ux"] {
  color: #fff;
  background-color: #837070;
}

#type-legend {
  gap: 3rem;
  margin: 0 2rem;
  display: flex;
}

#type-legend > div {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-size: .7em;
  display: flex;
}

#errors {
  color: #fff;
  background-color: #8b0000;
  border: 5px solid red;
  box-shadow: 3px 3px 10px #000000b3;
}

#errors::backdrop {
  background-color: #000000e6;
}

.error-message {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#errors h2 {
  margin: 0 0 1rem;
}

#errors ul {
  padding: .2rem 1rem .2rem 1.5rem;
}

#errors button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 0;
  border-radius: 15px;
  margin-top: 1.5rem;
  padding: .5rem 1.2rem;
}

#errors button:hover, #errors button:focus, #errors button:active {
  background-color: #00008b;
}

